.navigation {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 80px;
	padding: 0 1rem;
	background-color: var(--mainColor);
	color: var(--textColor);
}

.icon-and-title {
    font-size: 25px;
    cursor: pointer;
}

nav a {
	margin: 0 1rem;
	color: var(--textColor);
}

nav a:hover {
	color: var(--secondaryColor);
}

.navigation .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: var(--textColor);
	display: none;
	opacity: 0;
	font-size: 1.8rem;
}

.navigation div,
nav {
	display: flex;
	align-items: center;
}

@media only screen and (max-width: 900px) {
	.navigation .nav-btn {
		display: block;
		opacity: 1;
	}

	.navigation nav {
		position: fixed;
		top: -100vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--mainColor);
		transition: 1s;
	}

	.navigation .responsive_nav {
		transform: translateY(100vh);
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
}